.contain{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    justify-content: center;
}
.booking{
    max-width: 550px;
    height: 420px;
    background: rgba(0,0,0,0.5);
    padding: 28px;
    margin: 28px 28px 28px 28px;
    border-radius: 10px;
    background: url("../img/temp.jpeg");
    box-shadow: inset -2px 2px 2px white;
}
.booking h1{
    text-align: center;
    font-size: 26px;
    font-weight: 600;
    padding-bottom: 6px;
    color: white;
    text-shadow: 2px 2px 2px black;
    border-bottom: solid 1px white;
}
.main-user-info{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 20px 0;
}
.user-input-box:nth-child(2n){
    justify-content: end;
}
.user-input-box{
    display: flex;
    flex-wrap: wrap;
    width: 50%;
    padding-bottom: 15px;
}
.user-input-box label{
    width: 95%;
    color: white;
    font-size: 20px;
    font-weight: 400;
    margin: 5px 0;
}
.user-input-box input{
    height: 40px;
    width: 95%;
    border-radius: 7px;
    outline: none;
    border: 1px solid gray;
    padding: 0 10px;
}
.user-input select{
    height: 25px;
    width: 95%;
    border-radius: 7px;
    outline: none;
    border: 1px solid gray;
    padding: 0 10px;
}
.user-input{
    color: white;
    font-size: 20px ;
    font-weight: 400;
    border-radius: 1px solid white;
    display: flex;
    flex-wrap: wrap;
    width: 50%;
    padding-bottom: 15px;
}
.form-submit button{
    cursor: pointer;
    display: block;
    margin-left: 15rem;
    width: 100%;
    margin-top: 10px;
    font-size: 10px;
    padding: 5px;
    border: none;
    border-radius: 3px;
    color: black;
    background: rgba(63,114,76,0,7);

}
.card button{
    margin-top: 40px;
}
.form-submit button:hover{
    background: rgba(56,204,93,0.7);
    color: rgb(255,255,255);
}
.user-input-box textarea{
    font-size: 15px;
    height: 40px;
    width: 95%;
    border-radius: 7px;
    outline: none;
    border: 1px solid gray;
    padding: 0 10px;

}

.program_c{
    margin-top: 3rem;
    margin-left: 5rem;
    width: 95%;
    align-items: center;
    justify-content: space-between;
}
.temp_card{
    width: 80%;
    text-align: start;
    box-shadow: 0 5px 25px 2px rgba(0,0,0,0.1);
    border-radius: 7px;
    cursor: pointer;
    display: flex;
    padding: 1rem .8rem 1rem .8rem;
}
.temp_image{
    height: 300px;
    overflow: hidden;
    border-radius: 7px;
    padding: 10px;
}
.temp_image img{
    width: 100%;
    height: 100%;
    transition: 0.3s ease-in-out;
    border-radius: 7px;
}
.temp_image:hover img{
    transform: scale(1.1);
}