body{
    width:auto;
}
.container{
    display: flex;
    justify-content: space-between;

}
.main{
    margin: 4rem 6rem;
    text-align: start;
    width: 60%;
}
.main h1{
    padding-bottom: 1rem;
    font-size: 3rem;
    margin-top: 3rem;
}
.main p{
    padding-bottom: 2rem;
    font-size: 2rem;
    width: 95%;
}
.container2{
    width: 30%;
}
.maindiv{
    width: 90%;
    height: 300px;
    position: relative;
    top: 15%;
    background-image: url("/src/img/phool5.jpg");
    transform: translate(-50% 50%);
    background-size: 100% 100%;
    box-shadow: 1px 2px 10px 10px black;
    animation: slider 25s infinite linear;
    margin-bottom: 15rem;
}
.subdiv{
    animation: slider2 25s infinite linear;
    height: 400px;
}
@keyframes slider {
    0%{background-image: url("/src/img/phool1.jpg"); }
    20%{background-image: url("/src/img/phool2.jpg"); }
    40%{background-image: url("/src/img/phool3.jpg"); }
    60%{background-image: url("/src/img/phool4.jpg"); }
    80%{background-image: url("/src/img/phool5.jpg"); }
    100%{background-image: url("/src/img/phool7.jpg"); }
}
@keyframes slider2 {
    0%{background-image: url("/src/img/khichadi.jpg"); }
    20%{background-image: url("/src/img/khichadi1.jpg"); }
    40%{background-image: url("/src/img/khichadi2.jpg"); }
    60%{background-image: url("/src/img/khichadi3.jpg"); }
    80%{background-image: url("/src/img/khichadi4.jpg"); }
    100%{background-image: url("/src/img/khichadi5.jfif"); }
}