.about-container{
    margin: 4rem 6rem;
    text-align: start;
    display: flex;
}
.main{
    width: 100%;
}
.main h1{
    padding-bottom: 1rem;
    font-size: 3rem;
}
.main p{
    padding-bottom: 2rem;
    font-size: 2rem;
    width: 95%;
}
.main ul{
    list-style: circle;
    padding-bottom: 2rem;
    font-size: 2rem;
}
.program_card{
    margin-top: 3rem;
    margin-left: 5rem;
    width: 50%;
    display: flex;
    justify-content: space-between;
}
.t_card{
    width: 80%;
    text-align: start;
    box-shadow: 0 5px 25px 2px rgba(0,0,0,0.1);
    border-radius: 7px;
    cursor: pointer;
    padding: 1rem .8rem 1rem .8rem;
}
.t_image{
    height: 300px;
    overflow: hidden;
    border-radius: 7px;
    padding: 10px;
}
.t_image img{
    width: 100%;
    height: 100%;
    transition: 0.3s ease-in-out;
    border-radius: 7px;
}
.t_image:hover img{
    transform: scale(1.1);
}